.sidebar {
    position: fixed;
    top: 0;
    left: -300px;
    width: 300px;
    height: 100%;
    background-color: #f0f0f0;
    transition: all 0.3s ease-in-out;
    z-index: 9999;
}

.sidebar.open {
    left: 0;
}

.background {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.5);
    z-index: 9998;
    display: none;
}

.nav {
    padding: 20px;
}

.nav a {
    display: block;
    color: #333;
    text-decoration: none;
    padding: 10px;
    border-radius: 5px;
}

.nav a:hover {
    background-color: #eee;
}

.nav a.active {
    background-color: #333;
  color: #fff;
}
.close {
    position: absolute;
    top: 10px;
    right: 10px;
    cursor: pointer;
}

.close img {
    width: 20px;
    height: 20px;
}
